@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
html,body,div,span,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,abbr,address,cite,code,del,dfn,em,img,ins,kbd,q,samp,small,strong,sub,sup,var,b,i,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,figcaption,figure,footer,header,hgroup,menu,nav,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;outline:0;font-size:100%;vertical-align:baseline;background:transparent;font-weight:normal;letter-spacing:1px}​ *{box-sizing:border-box;text-decoration:none;list-style:none;color:inherit}​ *:focus{outline:none;border:none}​ body{line-height:1}​ article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}​ nav ul{list-style:none}​ blockquote,q{quotes:none}​ blockquote:before,blockquote:after,q:before,q:after{content:"";content:none}​ a{margin:0;padding:0;font-size:100%;vertical-align:baseline;background:transparent}​ ins{background-color:#ff9;color:#000;text-decoration:none}​ mark{background-color:#ff9;color:#000;font-style:italic;font-weight:bold}​ del{text-decoration:line-through}​ abbr[title],dfn[title]{border-bottom:1px dotted;cursor:help}​ table{border-collapse:collapse;border-spacing:0}​ hr{display:block;height:1px;border:0;border-top:1px solid #cccccc;margin:1em 0;padding:0}​ input,select{vertical-align:middle}​ textarea{resize:none}

.GNB{padding:0;margin:0;background:#42f566}.GNB__Inner{z-index:1000;background:#42f566;justify-content:space-evenly !important}.GNB__Inner::after{display:none !important}.GNB__Inner .item{font-size:1.2rem;color:white !important;cursor:pointer !important}

.Header{padding:2rem 10rem}.Header__Inner{display:flex;justify-content:space-between;align-items:center}.Header__Inner span{font-size:1.8rem;cursor:pointer}.Header__Inner .menu{cursor:pointer !important}.Header__Inner .input{width:200px !important}.table{padding:0 5rem;margin-bottom:4rem !important}.table::after{display:none !important}.table .item{padding:0 3rem !important;cursor:pointer !important}.table .item:hover{background:#dbffe2 !important}

